import React, { useState } from 'react';
import { Link } from 'react-router-dom'
// import { MdFingerprint } from 'react-icons/md'
import { FaBars, FaTimes } from 'react-icons/fa'
import './Navbar.css';
import { IconContext } from 'react-icons/lib';

// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { 
    FcServices, 
    FcHome,
    FcContacts,
    FcAbout
 } from "react-icons/fc";

function Navbar() {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    
    // Handling the toggle icons
    const handleClick = () => setClick(!click)
    const closeMobileMenu = () => setClick(false);
    
    const showButton = () => {
        if(window.innerWidth <= 960){
            setButton(false)
        } else {
            setButton(true)
        } 
    }

    window.addEventListener('resize', showButton)
    
    return (
        <>
        <IconContext.Provider value={{color: '#596bb2'}}> 
        {/* <div className="navbar"> */}
        <div className="pt-4">
            <div className="navbar-container container">
                <img href="/home__hero-row" src="https://lh3.googleusercontent.com/ALYBLKl2-DH35-5Y_SApWpuRioJru6TF-dxyw-gnISfhHhgdiqtrmSZf8kY_TYbWRnvRuIH7uvnMoDihh7k6JJn3diwtcoh46Dn54XiazSFSLq7E1VvVI5Gg352GVhnGYA-uTRcnAug9pWzVi7h_Qc910z6YRs364lGmImDyTfe7jAr6UQ_YdBmXV2U43PXMFsrkk9vmApAXtPr-iNsbR6w7erYezbfVy92xJEfB7AF5aH2Uf4Tj-fa5BrrdM4T3xifG-avbVNXFdh67i8q-hsyqPckFHRJd5C0LQicsPEUnYG9s2neVIITYbyLhZoXVApGlwseDMXVd-fizVJ8Ms9v5IvYk3af_DsGtkWg5rUmoRlKh8w_bIyv0bOfkJ9_LwHiYEYW0mHEnrWharG_oymJwX8Th6q99ulsdtVz3j11X9VXYMlFFNrIW40kKPnaldEkG5sFc01UqFWsOe-OErEY2lhaopWt5xC7ZBFZnj5VuWMWI3pKQrgEu8D4-3opRTqBgzrZAqYrx1Ak4Ga9ATf2ouPu-QNRqox9ux_9BqIl-YIWo7efLATReeQ9vSix7EVOu-8uijOTpxR1XypL3tEIFTKtotjAPtCrWNCO2qcRxQdxGTjWPkjdMwRV8C4yV0doANfU7xI3VaSzqmHrHbfGPTMqMIfq7GDtsEcZGigxuGnCNUTucem1EkH_a=w294-h259-no?authuser=0" alt="engine dome logo" onClick={closeMobileMenu}  className="myLogo" />
                {/* <Link to='/' className="navbar-logo" onClick={closeMobileMenu}>
                    <MdFingerprint className="navbar-icon" /> */}
                    {/* Engine Dome */}
                {/* </Link>  */}

                {/* This div toggles from X to Hamburger icons */}
                <div className="menu-icon" onClick={handleClick} >
                    {click ? <FaTimes /> : <FaBars />}
                </div>

                <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                    <li className="nav-item">
                        <Link to='/' className="nav-links" onClick={closeMobileMenu}>
                            <FcHome className="mb-1" size={20} /> Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to='/About' className="nav-links" onClick={closeMobileMenu}>
                        <FcAbout href="/aboutContainer" className="mb-1" size={20} /> About
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to='/Services' className="nav-links" onClick={closeMobileMenu}>
                        <FcServices className="mb-1" size={20} /> Services
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to='/contact' className="nav-links" onClick={closeMobileMenu}>
                            <FcContacts className="mb-1" size={20} /> Contact
                        </Link>
                    </li>
                </ul>
            </div>
        </div>
        </IconContext.Provider>
        </>
    )
}

export default Navbar;